<template>
    <div>
        <span>选择器优先级：</span>
        <span style="color:red;">内联样式 > ID选择器 > 标签属性选择器 > 类选择器 > 标签选择器 > </span>
        <p style="text-align:left;padding:0 20px;color:red;">||  用来分割示例</p>
        <div v-for="(item, index) in funList" :key="index" style="text-align:left;padding:0 20px;">
            <p>选择器：{{item.name}}</p>
            <p>示例一：{{item.example1}}</p>
            <p>示例二：：{{item.example2}}</p>
            <a href="www.baidu.com" class="myA">百度链接</a>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            funList: [
                {
                    name: '通配符选择器',
                    example1: '*{   }',
                    example2: '*.title{   }   ||   *[lang^=en] {   } : 匹配带有lang属性，并且lang属性值以en开头的元素'
                },
                {
                    name: '标签选择器',
                    example1: 'h1 {   }',
                    example2: 'span {   }'
                },
                {
                    name: '类选择器',
                    example1: '.mytitle {   }',
                    example2: '.app_container {   }'
                },
                {
                    name: 'ID选择器',
                    example1: '#app {   }',
                    example2: '#main {   }'
                },
                {
                    name: '标签属性选择器',
                    example1: 'a[title] {   }      ||    a[href="https://example.org"] {   }',
                    example2: 'a[href*="example"]{   }：属性值包含example  ||  a[href^="example":{   }:属性值以example开头  ||  a[href$="example":{   }:属性值以example结尾]'
                },
                {
                    name: '伪类选择器',
                    example1: ':hover {   }     ||  :active {   }',
                    example2: ':first-child {   }     ||    :nth-child {   }'
                },
                {
                    name: '伪元素选择器',
                    example1: '::after {   }      ||     ::before {   }',
                    example2: '::first-line {   }     ||      ::first-letter {   }'
                },
                {
                    name: '后代选择器',
                    example1: 'h1 .box {   }',
                    example2: '.main .box {   }'
                },
                {
                    name: '子代选择器',
                    example1: 'h1>.box{   }',
                    example2: 'header>div{   }'
                },
                {
                    name: '相邻兄弟选择器',
                    example1: 'p+h1{   }',
                    example2: 'p+span{   }'
                },
                {
                    name: '通用兄弟选择器',
                    example1: 'ul~span{   }',
                    example2: 'p~a{   }'
                }
            ]
        }
    },
    methods: {
        
    },
}
</script>
<style lang="scss">
    a {
        color:red;
    }
    .myA{
        color: blue;
    }
    a[href*="baidu"]{
        color:green;
    }
</style>