<template>
    <div>
        <h2 style="margin-block-start: 0;">css变量</h2>
        <div class="bg-pic">
            <h3 style="margin-block-start: 0;">更换背景</h3>
            <div>
                <img :src="require('/src/assets/bg01.jpg')" @click="changeBGImg" data-imgsrc="bg01.jpg">
                <img :src="require('/src/assets/bg02.jpg')" @click="changeBGImg" data-imgsrc="bg02.jpg">
                <img :src="require('/src/assets/bg03.jpg')" @click="changeBGImg" data-imgsrc="bg03.jpg">
                <img :src="require('/src/assets/bg04.jpg')" @click="changeBGImg" data-imgsrc="bg04.jpg">
            </div>
        </div>
        <div class="bg">
            <h3>更换主题肤色</h3>
            <input type="color" v-model="inputColor" style="cursor:pointer;" @change="changeBG">
            <div>
                <h4>选择主题颜色</h4>
                <div class="red" @click="changeBG" data-bgcol="red"></div>
                <div class="blue" @click="changeBG" data-bgcol="blue"></div>
                <div class="orange" @click="changeBG" data-bgcol="orange"></div>
                <div class="green" @click="changeBG" data-bgcol="green"></div>
                <div class="yellow" @click="changeBG" data-bgcol="yellow"></div>
                <div class="purple" @click="changeBG" data-bgcol="purple"></div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            inputColor: null
        }
    },
    methods: {
        changeBGImg(e){
            console.log('选择图片背景', e.target.dataset.imgsrc)
            let name = e.target.dataset.imgsrc  
            this.$store.commit('SET_IMGNAME',name)
        },
        changeBG(e){
            console.log('选择颜色', e.target.dataset.bgcol)
            document.querySelector('body').removeAttribute('theme-type')
            if (this.inputColor) {
                let c = document.documentElement.style.getPropertyValue('--bgColor')
                console.log('------',JSON.stringify(c))
                document.documentElement.style.setProperty('--bgColor',this.inputColor)
                document.documentElement.style.setProperty('--fColor','#2c3e50')
                this.inputColor = null
            } else {
                let c = e.target.dataset.bgcol
                document.documentElement.style.setProperty('--bgColor', c)
                document.documentElement.style.setProperty('--fColor','#ffffff')
            }
        }
    },
}
</script>
<style lang="scss" scoped>
    img{
        height: 150px;
        width: 200px;
        margin: 0 10px;
        cursor: pointer;
    }
    .red{
        background-color: red;
    }
    .blue {
        background-color: blue;
    }
    .green{
        background-color: green;
    }
    .yellow{
        background-color: yellow;
    }
    .orange{
        background-color: orange;
    }
    .purple{
        background-color: purple;
    }
    .red,.blue,.green,.yellow,.orange,.purple{
        width: 50px;
        height: 30px;
        display: inline-block;
        margin: 10px;
        cursor: pointer;
    }
</style>