<template>
    <div>
        <h2 style="margin-block-start: 0;">使用[]属性选择器改变父元素属性</h2>
        <div class="bg-pic">
            <h3 style="margin-block-start: 0;">更换背景</h3>
            <div>
                <img :src="require('/src/assets/bg07.jpg')" @click="changeBGImg" data-imgsrc="bg07.jpg">
                <img :src="require('/src/assets/bg08.jpg')" @click="changeBGImg" data-imgsrc="bg08.jpg">
                <img :src="require('/src/assets/bg09.jpg')" @click="changeBGImg" data-imgsrc="bg09.jpg">
                <img :src="require('/src/assets/bg10.jpg')" @click="changeBGImg" data-imgsrc="bg10.jpg">
            </div>
        </div>
        <div class="bg">
            <h3>更换主题肤色</h3>
            <div>
                <div class="orange" @click="changeBG" data-bgcol="orange"></div>
                <div class="purple" @click="changeBG" data-bgcol="purple"></div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            inputColor: null
        }
    },
    methods: {
        changeBGImg(e){
            console.log('选择图片背景', e.target.dataset.imgsrc)  
            let name = e.target.dataset.imgsrc  
            this.$store.commit('SET_IMGNAME',name)
        },
        changeBG(e){
            console.log('选择颜色', e.target.dataset.bgcol)
            let c = e.target.dataset.bgcol
            document.querySelector('body').setAttribute('theme-type',c)
        }
    },
}
</script>
<style lang="scss" scoped>
    img{
        height: 150px;
        width: 200px;
        margin: 0 10px;
        cursor: pointer;
    }
    .light{
        background-color: white;
    }
    .dark {
        background-color: black;
    }
    .green{
        background-color: green;
    }
    .yellow{
        background-color: yellow;
    }
    .orange{
        background-color: orange;
    }
    .purple{
        background-color: purple;
    }
    .light,.dark,.green,.yellow,.orange,.purple{
        width: 50px;
        height: 30px;
        display: inline-block;
        margin: 10px;
        cursor: pointer;
    }
</style>