<template>
    <div>
        <div>{{originArray1}}</div>
        <div>{{originArray2}}</div>
        <div v-for="(item, index) in funList" :key="index" style="text-align:left;padding:0 20px;">
            <h3 style="color: red;" v-if="item.name == 'forEach'">
                /*forEach、map、filter、every、some、find，原数组均不改变。(如果数组内元素是基本类型，值不变，若是引用值，引用地址不变)*/</br>
                /*forEach、map、filter、every、some、find，原数组内元素为引用值时，引用地址不变，引用对象内容可以变化*/
            </h3>
            <p>方法名：{{item.name}}</p>
            <p>介绍：{{item.introduce}}</p>
            <p>原数组：{{item.origin}}</p>
            <p>返回值：{{item.return}}</p>
            <button @click="item.optiation(index)">执行</button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            originArray1: [1,3,4,6,5],
            originArray2: [{name: '刘某',age: 20},{name: '张某',age: 23}],
            funList: [
                {
                    name: 'shift',
                    introduce: 'arrayObject.shift();无参数，删除数组的第一项，返回删除项，原数组改变。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'unshift',
                    introduce: 'arrayObject.unshift(num1[,num2...]);将元素插入到数组的起始位置,返回新数组的长度，原数组改变。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'splice',
                    introduce: 'arrayObject.splice(index,length[,num1,num2...]);index参数必选，从索引index处删除length个（若length不传，则从index处删除至末尾），用num1，num2...代替删除的内容，返回删除项，原数组改变。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'slice',
                    introduce: 'arrayObject.slice(start,end);截取，返回截取后的数组，原数组不变。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'concat',
                    introduce: 'arrayObject.concat(num1[,num2...]);合并数组，返回合并后的数组，原数组不改变。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'sort',
                    introduce: 'arrayObject.sort(function);对数组的元素排序，返回排序后的数组，原数组改变',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'join',
                    introduce: 'arrayObject.join(separator)将数组的元素组成一个字符串，以separator分开，若不传参数，则默认以逗号分开;返回组成后的字符串，原数组不变。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'forEach',
                    introduce: 'arrayObject.forEach(callback[,thisObj])（callback参数为function，thisObj参数为this指向）遍历数组中的每一项，无返回值。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'map',
                    introduce: 'arrayObject.map(callback[,thisObj])：映射，基本用法跟forEach类似，但是有返回值，返回值是一个数组，记录了每一个回调函数的返回值。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'filter',
                    introduce: 'arrayObject.filter(callback[,thisObj])：过滤，返回过滤后的数组，基本用法跟map非常类似，callback返回值弱等于（==）true或者fasle,若为true,则表示通过（该元素保留下来了，继续查找下一项），false表示不通过(继续查找下一项)。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'every',
                    introduce: 'every()是对数组中每一项运行给定函数，如果该函数对每一项返回true,则返回true。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'some',
                    introduce: 'some()是对数组中每一项运行给定函数，如果该函数对任一项返回true，则返回true。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'find',
                    introduce: 'arrayObject.find(callback[,thisObj]);查找，返回符合条件的数组，基本用法跟filter类似，callback返回值弱等于（==）true或者fasle,若为true,则表示找到该项，并返回，不再继续查找下一项，false表示未找到(继续查找下一项)。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                }
            ]
        }
    },
    methods: {
        handle(i){
            console.log('方法下标', i)
            let originArray01 = JSON.parse(JSON.stringify(this.originArray1))
            let originArray02 = JSON.parse(JSON.stringify(this.originArray2))
            let r;
            switch (i) {
                case 0:
                    r = originArray01.shift()
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 1:
                    r = originArray01.unshift(2)
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 2:
                    r = originArray01.splice(1,2,8,8,8,8)
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 3:
                    r = originArray01.slice(1,3)
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 4:
                    r = originArray01.concat(originArray01,originArray02)
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 5:
                    r = originArray01.sort()
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 6:
                    r = originArray01.join('，')
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 7:
                    // r = []
                    // originArray01.forEach(e => {r.unshift(e)})
                    // this.funList[i].origin = originArray01
                    // this.funList[i].return = r
                    r = []
                    originArray02.forEach(e => {
                        e.name = 'change'
                        r.push(e)
                    })
                    this.funList[i].origin = originArray02
                    this.funList[i].return = r
                    break;
                case 8:
                    // r =  originArray01.map(a => {
                    //     return a + 1
                    // })
                    // this.funList[i].origin = originArray01
                    // this.funList[i].return = r
                    r =  originArray02.map(a => {
                        return a.age =66
                    })
                    this.funList[i].origin = originArray02
                    this.funList[i].return = r
                    break;
                case 9:
                    r =  originArray01.filter(a => {
                        return a > 4
                    })
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 10:
                    // r =  originArray01.every(a => {
                    //     return a > 4
                    // })
                    // this.funList[i].origin = originArray01
                    // this.funList[i].return = r
                    // break;
                    r =  originArray02.every(a => {
                        a.age += 1
                        return a.age > 30
                    })
                    this.funList[i].origin = originArray02
                    this.funList[i].return = r
                    break;
                case 11:
                    r =  originArray01.some(a => {
                        return a > 4
                    })
                    this.funList[i].origin = originArray01
                    this.funList[i].return = r
                    break;
                case 12:
                    // r =  originArray01.find(a => {
                    //     return a > 4
                    // })
                    // this.funList[i].origin = originArray01
                    // this.funList[i].return = r
                    r =  originArray02.find(a => {
                        a.age += 18
                        return a.age > 40
                    })
                    this.funList[i].origin = originArray02
                    this.funList[i].return = r
                    break;
                default:
                    break;
            }
        }
    },
}
</script>