<template>
    <div>
        <h2 style="margin-block-start: 0;">css类切换</h2>
        <div class="bg-pic">
            <h3 style="margin-block-start: 0;">更换背景</h3>
            <div>
                <img :src="require('/src/assets/bg01.jpg')" @click="changeBGImg" data-imgsrc="bg01.jpg">
                <img :src="require('/src/assets/bg02.jpg')" @click="changeBGImg" data-imgsrc="bg02.jpg">
                <img :src="require('/src/assets/bg05.jpg')" @click="changeBGImg" data-imgsrc="bg05.jpg">
                <img :src="require('/src/assets/bg06.jpg')" @click="changeBGImg" data-imgsrc="bg06.jpg">
            </div>
        </div>
        <div class="bg">
            <h3>更换主题肤色</h3>
            <div>
                <div class="light" @click="changeBG" data-bgcol="light"></div>
                <div class="dark" @click="changeBG" data-bgcol="dark"></div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            inputColor: null
        }
    },
    methods: {
        changeBGImg(e){
            console.log('选择图片背景', e.target.dataset.imgsrc)  
            let name = e.target.dataset.imgsrc  
            this.$store.commit('SET_IMGNAME',name)
        },
        changeBG(e){
            console.log('选择颜色', e.target.dataset.bgcol)
            document.querySelector('body').removeAttribute('theme-type')
            let c = e.target.dataset.bgcol
            this.$store.commit('SET_THEME', c)
        }
    },
}
</script>
<style lang="scss" scoped>
    img{
        height: 150px;
        width: 200px;
        margin: 0 10px;
        cursor: pointer;
    }
    .light{
        background-color: white;
    }
    .dark {
        background-color: black;
    }
    .green{
        background-color: green;
    }
    .yellow{
        background-color: yellow;
    }
    .orange{
        background-color: orange;
    }
    .purple{
        background-color: purple;
    }
    .light,.dark,.green,.yellow,.orange,.purple{
        width: 50px;
        height: 30px;
        display: inline-block;
        margin: 10px;
        cursor: pointer;
    }
</style>