<template>
    <div>
        <div>{{originNumber}}--->{{typeof originNumber}}</div>
        <div>{{originNumber1}}--->{{typeof originNumber1}}</div>
        <div v-for="(item, index) in funList" :key="index" style="text-align:left;padding:0 20px;">
            <p>方法名：{{item.name}}</p>
            <p>介绍：{{item.introduce}}</p>
            <p>元数组：{{item.origin}}</p>
            <p>返回值：{{item.return}}</p>
            <p>返回值类型：{{item.type}}</p>
            <button @click="item.optiation(index)">执行</button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            originNumber: 3141592674846666,
            originNumber1: 0.6,
            funList: [
                {
                    name: 'toString',
                    introduce: 'toString() 把数字转换为字符串。',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'toFixed',
                    introduce: 'toFixed() 返回字符串值，它包含了指定位数小数的数字。',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'Math.ceil',
                    introduce: 'Math.ceil() 方法可对一个数进行上舍入',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'Math.floor',
                    introduce: 'Math.floor() 方法可对一个数进行下舍入',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'isInteger',
                    introduce: 'Number.isInteger() 用于判断其参数是为整数,Number的静态方法',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'trunc',
                    introduce: 'Math.trunc()方法用于去除一个数的小数部分，返回整数部分',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'random',
                    introduce: 'Math.random()获取0~1之间的随机小数，包含0，但不包含1',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                },
                {
                    name: 'round',
                    introduce: 'Math.round()对数字进行四舍五入取整',
                    origin: '',
                    return:'',
                    type: '',
                    optiation: this.handle
                }
            ]
        }
    },
    methods: {
        handle(i){
            console.log('方法下标', i)
            let originString01 = JSON.parse(JSON.stringify(this.originNumber))
            let originString02 = JSON.parse(JSON.stringify(this.originNumber1))
            let r;
            switch (i) {
                case 0:
                    r = originString01.toString()
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 1:
                    r = originString01.toFixed(2)
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 2:
                    r = Math.ceil(originString02)
                    this.funList[i].origin = originString02
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 3:
                    r = Math.floor(originString02)
                    this.funList[i].origin = originString02
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 4:
                    r = Number.isInteger(originString02)
                    this.funList[i].origin = originString02
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 5:
                    r = Math.trunc(originString02)
                    this.funList[i].origin = originString02
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 6:
                    r = Math.random()
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                case 7:
                    r = Math.round(originString02)
                    this.funList[i].origin = originString02
                    this.funList[i].return = r
                    this.funList[i].type = typeof r
                    break;
                default:
                    break;
            }
        }
    },
}
</script>