<template>
  <div id="app">
    <nav :class="[$store.state.theme]" style="cursor:pointer;">
      <span @click="changeTab(1)">换肤(一)</span> |
      <span @click="changeTab(2)">换肤(二)</span> |
      <span @click="changeTab(3)">换肤(三)</span> |
      <span @click="changeTab(4)">数组</span> |
      <span @click="changeTab(5)">字符串</span> |
      <span @click="changeTab(6)">Number</span> |
      <span @click="changeTab(7)">CSS选择器</span> |
    </nav>
    <div class="app-container" :style="bgImg">
      <div :class="['app-main', $store.state.theme]">
        <component :is='componentN'></component>
      </div>
    </div>
  </div>
</template>
<script>
import HomePage from "./views/HomePage"
import ChangeSkin from '@/views/ChangeSkin'
import ChangeSkinThree from '@/views/ChangeSkinThree'
import Array from '@/views/Array'
import String from '@/views/String'
import Number from '@/views/Number'
import Css from '@/views/Css'
export default {
  components: {HomePage,ChangeSkin,ChangeSkinThree,Array,String,Number,Css},
  data(){
    return {
      componentN: 'HomePage'
    }
  },
  computed: {
    bgImg(){
      let name = this.$store.state.imgName
      let url = require(`/src/assets/${name}`)
      return {backgroundImage: `url(${url})`}
    }
  },
  methods: {
    changeTab(num){
      console.log('Tab----',num)
      switch (num) {
        case 1:
          this.componentN = "HomePage"
          break;
        case 2:
          this.componentN = "ChangeSkin"
          break;
        case 3:
          this.componentN = "ChangeSkinThree"
          break;
        case 4:
          this.componentN = "Array"
          break;
        case 5:
          this.componentN = "String"
          break;
        case 6:
          this.componentN = "Number"
          break;
        case 7:
          this.componentN = "Css"
          break;
      }
    }
  },
}
</script>
<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  font-weight: bold;
  color: var(--fColor);
}
nav.dark{
  position: relative;
  padding: 20px 30px;
  background-color: var(--navBgColor);
  a {
    font-weight: bold;
    color: #2c3e50;
    &.span-exact-active {
      color: #42b983;
    }
  }
}
nav.light{
  position: relative;
  padding: 20px 30px;
  background-color: antiquewhite;
  a {
    font-weight: bold;
    color: black;
    &.span-exact-active {
      color: red;
    }
  }
}
.app-container{
  position: relative;
  min-height: 90vh;
  // background-image: url('./assets/bg01.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}
.app-main.dark{
  position: relative;
  height: 100%;
  width: 70%;
  margin: auto;
  position: relative;
  color: #fff;
  background-color: var(--bgColor);
  opacity: .8;
}
.app-main.light{
  position: relative;
  height: 100%;
  width: 70%;
  margin: auto;
  position: relative;
  background-color: rgb(209, 203, 203);
  opacity: .8;
}

