import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../views/HomePage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  {
    path: '/changeSkin',
    name: 'ChangeSkin',
    component: () => import(/* webpackChunkName: "about" */ '../views/ChangeSkin.vue')
  },
  {
    path: '/changeSkinThree',
    name: 'ChangeSkinThree',
    component: () => import(/* webpackChunkName: "about" */ '../views/ChangeSkinThree.vue')
  },
  {
    path: '/Array',
    name: 'MyArray',
    component: () => import(/* webpackChunkName: "about" */ '../views/Array.vue')
  },
  {
    path: '/String',
    name: 'MyString',
    component: () => import(/* webpackChunkName: "about" */ '../views/String.vue')
  },
  {
    path: '/Number',
    name: 'MyNumber',
    component: () => import(/* webpackChunkName: "about" */ '../views/Number.vue')
  },
  {
    path: '/Css',
    name: 'MyCss',
    component: () => import(/* webpackChunkName: "about" */ '../views/Css.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
