<template>
    <div>
        <div>{{originString}}</div>
        <div v-for="(item, index) in funList" :key="index" style="text-align:left;padding:0 20px;">
            <p>方法名：{{item.name}}</p>
            <p>介绍：{{item.introduce}}</p>
            <p>原字符串：{{item.origin}}</p>
            <p>返回值：{{item.return}}</p>
            <button @click="item.optiation(index)">执行</button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            originString: '夜晚的天上有星星、月亮、云，还有化作星星的你！    ',
            funList: [
                {
                    name: 'charAt',
                    introduce: 'charAt() 方法获取到的是指定位置的字符。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'indexOf',
                    introduce: 'indexOf()：查找某个字符，有则返回第一次匹配到的位置，否则返回-1',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'includes',
                    introduce: 'includes()：该方法用于判断字符串是否包含指定的子字符串。如果找到匹配的字符串则返回 true，否则返回 false',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'startsWith',
                    introduce: '该方法用于检测字符串是否以指定的子字符串开始。如果是以指定的子字符串开头返回 true，否则 false。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'endsWith',
                    introduce: '该方法用来判断当前字符串是否是以指定的子字符串结尾。如果传入的子字符串在搜索字符串的末尾则返回 true，否则将返回 false。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'concat',
                    introduce: 'concat() 方法用于连接两个或多个字符串。该方法不会改变原有字符串，会返回连接两个或多个字符串的新字符串。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'split',
                    introduce: 'split() 方法用于把一个字符串分割成字符串数组。该方法不会改变原始字符串。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'slice',
                    introduce: 'slice() 方法用于提取字符串的某个部分，并以新的字符串返回被提取的部分。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'substr',
                    introduce: 'substr() 方法用于在字符串中抽取从开始下标开始的指定数目的字符。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'replace',
                    introduce: 'replace()：该方法用于在字符串中用一些字符替换另一些字符，或替换一个与正则表达式匹配的子串。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'search',
                    introduce: 'search()方法用于检索字符串中指定的子字符串，或检索与正则表达式相匹配的子字符串。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'trim',
                    introduce: 'trim() 方法用于移除字符串首尾空白符，该方法不会改变原始字符串',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'repeat',
                    introduce: 'repeat() 方法返回一个新字符串，表示将原字符串重复n次',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
                {
                    name: 'padStart、padEnd',
                    introduce: 'padStart()和padEnd()方法用于补齐字符串的长度。如果某个字符串不够指定长度，会在头部或尾部补全。',
                    origin: '',
                    return:'',
                    optiation: this.handle
                },
            ]
        }
    },
    methods: {
        handle(i){
            console.log('方法下标', i)
            let originString01 = JSON.parse(JSON.stringify(this.originString))
            let r;
            switch (i) {
                case 0:
                    r = originString01.charAt(2)
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 1:
                    r = originString01.indexOf('的')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 2:
                    r = originString01.includes('的')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 3:
                    r = originString01.startsWith('夜')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 4:
                    r = originString01.endsWith('夜')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 5:
                    r = originString01.concat('我是后者拼接！！！')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 6:
                    r = originString01.split('，')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 7:
                    r = originString01.slice(2,5)
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 8:
                    r = originString01.substr(2,5)
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 9:
                    r = originString01.replace('夜晚','Today Night')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 10:
                    r = originString01.search('星星')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 11:
                    r = originString01.trim()
                    this.funList[i].origin = this.originString.length
                    this.funList[i].return = r.length
                    break;
                case 12:
                    r = originString01.repeat(3)
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                case 13:
                    r = originString01.padEnd(100,'我是补齐！')
                    this.funList[i].origin = originString01
                    this.funList[i].return = r
                    break;
                default:
                    break;
            }
        }
    },
}
</script>